/*eslint-env jquery*/
import React, { Component, useState } from 'react'
import { Tabs, Tab } from 'react-bootstrap'
import ProductsList from './ProductsList'


const Products = props => {
  const [activeTab, setActiveTab] = useState('productsOnly')

  return (

    <Tabs transition={false}
      id="controlled-tab-example"
      activeKey={activeTab}
      onSelect={activeTab => setActiveTab(activeTab)}
    >
      <Tab eventKey="productsOnly" title="محصولات">
        {activeTab === 'productsOnly' ?
          <ProductsList hasShop={false} />
          : ''}
      </Tab>
      <Tab eventKey="productsWithShops" title="محصولات فروشگاه ها">
        {activeTab === 'productsWithShops' ?
          <ProductsList hasShop={true} />
          : ''}
      </Tab>
    </Tabs>
  )
}
export default Products;