import axios from 'axios';
import { handleSuccess, handleError } from './axiosHandler';
import { authHeader, authHeaderJson } from '../_helpers/auth-header';

import  config  from '../config';
const base_url = config.apiUrl;

const model = 'users/';

export const usersService = {
  getAllPaginate,
  getAllByUserType,
  getById,
  add,
  edit,
  checkUserName,
  checkEmail,
  delete: _delete,
  toggleStatus
};

function getAllPaginate(dto) {
  console.log(dto);
  
  const url = base_url
    + 'users?page=' + dto.page
    + '&per_page=' + dto.pageSize
    + (dto.first_name ? '&first_name=' + dto.first_name : '')
    + (dto.last_name ? '&last_name=' + dto.last_name : '')
    + (dto.dob ? '&dob=' + dto.dob : '')
    + (dto.mobile ? '&mobile=' + dto.mobile : '')
    + (dto.credit ? '&credit=' + dto.credit : '')
    + (dto.state_id ? '&state_id=' + dto.state_id : '')
    + (dto.city_id ? '&city_id=' + dto.city_id : '')
    + (dto.order ? '&order=' + dto.order : '')
    + (dto.desc != undefined ? '&desc=' + dto.desc : '')
  console.log(url);

  axios.interceptors.response.use(handleSuccess, handleError);
  return axios.get(url, { headers: authHeaderJson() });
}

function getAllByUserType(userType) {
  const url = base_url + model + 'getAllByUserType/' + userType;
  console.log(url, userType);
  
  axios.interceptors.response.use(handleSuccess, handleError);
  return axios.get(url, { headers: authHeader()});
}

function getById(id) {
  const url = base_url + model + id;
  axios.interceptors.response.use(handleSuccess, handleError);
  return axios.get(url, { headers: authHeader()});
}

function checkUserName(userName) {
  const url = base_url + 'AspNetUsers/checkUserName/' + userName;
  axios.interceptors.response.use(handleSuccess, handleError);
  return axios.get(url, { headers: authHeader()});
}

function checkEmail(email) {
  const url = base_url + 'AspNetUsers/checkEmail/' + email;
  axios.interceptors.response.use(handleSuccess, handleError);
  return axios.get(url, { headers: authHeader()});
}

function add(dto) {
  const url = base_url + model + 'addUser';
  axios.interceptors.response.use(handleSuccess, handleError);
  let user = JSON.parse(localStorage.getItem('user'));

  let headers = { 'Content-Type': 'application/json; charset=utf-8', 'Authorization': 'Bearer ' + user.token };
  return axios.post(url, dto, { headers: headers});
}

function edit(id, form) {
  const url = base_url + model + id;
  axios.interceptors.response.use(handleSuccess, handleError);
  return axios.put(url, form, { headers: authHeaderJson()});
}

function toggleStatus(dto) {
  const url = base_url + model + 'toggleStatus';
  axios.interceptors.response.use(handleSuccess, handleError);
  return axios.post(url, dto, { headers: authHeaderJson()});
}

function _delete(id) {
  const url = base_url + model + id;
  axios.interceptors.response.use(handleSuccess, handleError);
  return axios.delete(url, { headers: authHeader()});
}
