import React, { Component } from 'react'
// import PropTypes from 'prop-types'
// import ReactSelect from 'react-select'
import { shopsService } from '../../_services/shops.service'
import AsyncSelect from 'react-select';


class AsyncShopSelect extends Component {

  constructor(props) {
    super(props);
    this.state = {
      loading: false,
      defaultValue: '',
      data: [],
      value: '',
      keyword: '',
    };
  }

  // componentWillMount() {
  //   this.getData();
  // }

  getData() {
    // var dto = {
    //   "page": 1,
    //   "pageSize": 10,
    //   "brandName": "",
    //   "categoryTypeIds": []
    // }
    // brandService.getAll(JSON.stringify(dto))
    //   .then((res) => {
    //     console.log(res.data)
    //     let data = res.data.result.map(elm => ({ "label": elm.name, "value": elm.id }))
    //     this.setState({ data: data, loading: false, isSubmitting: false });
    //   }).catch((error) => { console.log(error) });
  };

  handleChange = e => {
    this.props.onChange(e);
  }

  handleInputChange = (val) => {

    if (val && val.length > 0) {
      this.setState({ data: [], loading: true, isSubmitting: false });

      shopsService.find_shops(val)
        .then((res) => {

          let data = res.data.shops.map(elm => ({ "label": elm.name, "value": elm.id }))

          this.setState({ data: data, loading: false, isSubmitting: false });
        })
        .catch((error) => {
          // console.log(error);
          this.setState({ data: [], loading: false, isSubmitting: false })
        });
    }

  }

  componentWillReceiveProps(nextProps) {

    if (nextProps.value !== this.state.value) {

      this.setState({ value: nextProps.value });

    }

  }

  render() {
    const customStyles = {
      control: (provided) => ({
        ...provided,
        background: '#f6f6f6',
        borderRadius: 20,
        boxShadow: 'none',
        border: 'none',
        paddingRight: '12',
        border: '1px solid #e1e1e1',
      }),
      indicatorSeparator: () => ({
        display: 'none'
      }),
    }

    const { loading, data, value, keyword } = this.state;
    const { disabled, required, placeholder, tabIndex, isClearable } = this.props;

    return (
      <AsyncSelect
        cacheOptions
        tabIndex={tabIndex}
        classNamePrefix="select"
        // loadOptions={this.loadOptions}
        // defaultOptions
        // menuIsOpen={true}
        isClearable={isClearable}
        placeholder={placeholder}
        isDisabled={disabled}
        styles={customStyles}
        options={data}
        onInputChange={this.handleInputChange}
        className={required ? "select required" : "select"}
        value={data ? data.filter(e => e.value === value)[0] : ''}
        onChange={this.handleChange}
        noOptionsMessage={() => ((keyword.length > 0 && (!data || data.length === 0)) ? "نتیجه ای یافت نشد" : "نام شهر مورد نظر خود را وارد نمایید")}
        loadingMessage={() => "در حال دریافت اطلاعات از سرور"}
        isLoading={loading}
      />
    );
  }
}
export default AsyncShopSelect;
